// 获取公共域名地址
import api from '@/common/common.js';
export default {
	data() {
		return {
			
		}
	},
	methods: {
		appOAuthLogin(e) {
			// 获取用户的登录类型
			console.log(e)
			
			const logintype = e.currentTarget.dataset.logintype;
			
			const qq = api.qq;
			const url = api.api;
			
			// 授权登录
			uni.login({
				provider:logintype,   
				success: res => {
					// 授权登录成功后,获取用户的信息
					uni.getUserInfo({
						provider:logintype,
						success: info => {
							const userInfo = info.userInfo;
							let face = "";
							let nickname = "";
							let openIdUid = "";
							if (logintype == "weixin") {
								face = userInfo.avatarUrl;
								nickname = userInfo.nickName;
								openIdUid = userInfo.openId;
							} else if (logintype == "qq") {
								openIdUid = userInfo.openId;
								nickname = userInfo.nickname;
								face = userInfo.figureurl_qq_2;
							} else if (logintype == "sinaweibo") {
								openIdUid = userInfo.id;
								nickname = userInfo.nickname;
								face = userInfo.avatar_large;
							}
							// 调用开发者后台,执行一键注册或登录
							uni.request({
								url: url + '/appUnionLogin/' + logintype + '?qq=' + qq,
								data: {
									"openIdOrUid": openIdUid,
									"nickname": nickname,
									"face": face
								},
								method: "POST",
								success: (result) => {
									if (result.data.status == 200) {
										const userInfo = result.data.data;
										// 保存用户信息到全局的缓存中
										uni.setStorageSync("globalUser", userInfo);
										// 切换页面跳转
										uni.switchTab({
											url: "../me/me"
										});
									}
								}
							})
						}
					})
				}
			})
		},
		// 实现在微信小程序的微信登录
		wxLogin(e) {
			// 通过微信开放能力,获得微信用户的基本信息
			const userInfo = e.detail.userInfo;
			const qq = api.qq;
			const url = api.api;
			console.log('....')
			console.log(e)
		
			// 实现微信登录
			uni.login({
				provider: "weixin",
				success: loginResult => {
					// 获得微信登录的code:授权码
					console.log(loginResult)
					const code = loginResult.code;
					console.log(code)
					// 设置登录到哪个对应的微信小程序
					const loginToWhichMP = 1;
					uni.request({
						url: url + '/mpWXLogin/' + code + '?qq=' + qq,
						data: {
							"avatarUrl": userInfo.avatarUrl,
							"nickName": userInfo.nickName,
							"whichMP": loginToWhichMP
						},
						method: "POST",
						success: res => {
							console.log(res);
							const userInfo = res.data.data;
							// 保存用户信息到全局的缓存中
							uni.setStorageSync("globalUser", userInfo);
							// 切换页面跳转
							uni.switchTab({
								url: "../me/me"
							});
						}
					})
				}
			});
		},
		formSumit(e) {
			const qq = api.qq;
			const url = api.api;
			const username = e.detail.value.username;
			const password = e.detail.value.password;
			// 发起注册/登录请求
			uni.request({
				url: url + '/user/registOrLogin?qq=' + qq,
				data: {
					"username": username,
					"password": password
				},
				method: 'POST',
				success: res => {
					if (res.data.status == 200) {
						const userInfo = res.data.data;
						// 保存用户信息到全局的缓存中
						uni.setStorageSync("globalUser", userInfo);
						// 切换页面跳转
						uni.switchTab({
							url: "../me/me"
						});
					} else if (res.data.status == 500) {
						uni.showToast({
							title: res.data.msg,
							duration:2000,
							image:"../../static/icos/error.png"
						});
					}
				},
				fail: () => {
					console.log('登录或注册失败');
				}
			});
		}
	}
}
